html {
	@include r($lg) {
		font-size: (15rem/16); }
	@include r($md) {
		font-size: (14rem/16); } }

img {
	display: inline-block;
	max-width: 100%;
	height: auto; }

textarea {
	resize: none; }

.form-control, .input-group-text {
	font-weight: 300; }

.form-control, .input-group-text {
	@include placeholder {
		font-weight: 300;
		color: rgba(51, 51, 51, .5); } }

.form-control:disabled,
.form-control[readonly] {
	background-color: $white;
	border-color: $light; }

.form-row {
	margin-left: -8px;
	margin-right: -8px; }

.form-row > .col, .form-row>[class*=col-] {
    padding-right: 8px;
    padding-left: 8px; }

.btn {
	color: $white;
	line-height: normal;
	box-shadow: 0 1px 2px 0 rgba(51, 51, 51, 0.6);
	font-weight: bold; }

.btn-primary:hover {
	border-color: #e09b09;
	color: $white;
	background-color: #e09b09;
	box-shadow: 0 1px 2px 0 rgba(51, 51, 51, 0.6); }

.btn-primary:focus,
.btn-primary:active,
.btn-primary:not(:disabled):not(.disabled):active:focus {
	background-color: #faac09;
	border-color: #faac09;
	box-shadow: 0 1px 2px 0 rgba(51, 51, 51, 0.6); }


.btn-info {
	padding-right: 1rem;
	padding-left: 1rem;
	box-shadow: none;
	.icon-glass {
		width: 20px;
		height: 20px; } }

.btn-info:hover,
.btn-info:focus,
.btn-info:not(:disabled):not(.disabled).active,
.btn-info:not(:disabled):not(.disabled):active {
	box-shadow: none;
	border-color: $white;
	background-color: $white; }

.btn.disabled,
.btn:disabled {
	color: $white;
	background-color: #d8d8d8;
	border-color: #d8d8d8;
	opacity: .6; }

.btn-outline-primary {
	color: $dark;
	box-shadow: none; }

.btn-outline-primary:hover {
	box-shadow: none; }

.btn-outline-primary:focus,
.btn-outline-primary:active {
	color: $white;
	box-shadow: none; }

.btn-outline-primary:focus {
	color: $dark; }

.btn-outline-primary:not(:disabled):not(.disabled).active,
.btn-outline-primary:not(:disabled):not(.disabled):active {
	color: $white; }

.btn-link {
	color: $dark;
	font-size: 30px;
	font-weight: bold;
	line-height: normal;
	text-decoration: none;
	box-shadow: none;
	position: relative;
	z-index: 2;
	padding: 0; }


.btn-link:hover,
.btn-link:focus {
	text-decoration: none;
	background-color: transparent;
	color: $dark;
	border-color: transparent;
	box-shadow: none; }

.btn-link:before,
.btn-link:after {
	position: absolute;
	content: '';
	width: 50%;
	height: 50%;
	background: transparent;
	z-index: 1;
	transition: $transition-base; }

.btn-link:before {
	border-top: 2px solid $yellow;
	top: -2px;
	left: -2px; }

.btn-link:after {
	border-bottom: 2px solid $yellow;
	right: -2px;
	bottom: -2px; }

.btn-link:hover:before,
.btn-link:hover:after {
	width: 100%; }

.btn-dark {
	box-shadow: none;
	font-weight: 300; }




//header
.main-header {
	padding: 13px 0;
	box-shadow: inset 0 -2px 0 0 rgba(35, 35, 35, 0.48); }


.logo {
	display: inline-block;
	text-decoration: none;
	margin-right: auto;
	&-img {
		margin-right: 16px;
		width: 70px;
		height: 70px;
		object-fit: contain;
		display: inline-block;
		vertical-align: middle; }
	&-text {
		font-size: 1rem;
		font-weight: bold;
		line-height: normal;
		display: inline-block;
		vertical-align: middle;
		color: $white;
		margin: 0; }
	&:hover {
		text-decoration: none; } }

//hero
.hero {
	background: url('../img/bg.jpg') no-repeat center center $light / cover;
	padding-top: 186px;
	padding-bottom: 186px;
	position: relative;
	@include r($md) {
		padding: 100px 0; }
	&-title {
		font-size: 48px;
		line-height: normal;
		color: $white;
		margin-bottom: 30px;
		text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
		@include r($md) {
			font-size: 24px;
			margin-bottom: 15px; } } }

.input-search,
.hero .btn-info {
	height: 53px; }

.input-search,
.input-search:focus {
	border: 2px solid $white; }

.main-content ~ .main-footer {
	margin-top: 96px;
	@include r($md) {
		margin-top: 48px; } }

//feedback
.feedback {
	padding: 96px 0;
	@include r($md) {
		padding: 48px 0; }
	&-text {
		font-size: 28px;
		line-height: normal;
		font-weight: bold;
		color: #373a3d;
		@include r($lg) {
			text-align: center; }
		@include r($md) {
			font-size: 22px; } }
	&-info {
		color: $dark;
		line-height: normal;
		font-weight: normal;
		font-size: 16px;
		@include r($lg) {
			text-align: center; } } }

//footer
.main-footer {
	padding: 38px; }

.copyright {
	color: #fff;
	font-weight: bold;
	font-size: 1rem; }

//sliders

.ui-slide {
	position: relative; }

.tip-slide {
	padding: 30px;
	padding-bottom: 0;
	z-index: 100;
	@include r($xl) {
		padding: 25px;
		padding-bottom: 0; }
	@include r($lg) {
		padding: 20px;
		padding-bottom: 0; }
	&-title {
		font-size: 28px;
		font-weight: bold;
		line-height: normal;
		color: $light;
		margin-bottom: 20px;
		@include r($xl) {
			font-size: 24px; } }
	&-text {
		margin-bottom: 0;
		min-height: 85px;
		font-size: 1rem;
		line-height: normal;
		font-weight: 300;
		color: $white;
		@include r($lg) {
			min-height: auto; } } }

$slide-width: 1110;
$slide-height: 624;

.swiper-container-nested {
	position: absolute;
	z-index: 100;
	background-color: $dark;
	width: (53000/$slide-width + %);
	@include r($md) {
		display: none; } }

.swiper-container-nested-1 {
	left: (7000/$slide-width + %);
	top: (14000/$slide-height + %); }

.swiper-container-nested-2 {
	left: (6000/$slide-width + %);
	top: (12000/$slide-height + %); }

.swiper-container-nested-3 {
	left: (7000/$slide-width + %);
	top: (23000/$slide-height + %);

 }	// min-height: (23000%/$slide-height)

.swiper-btns-wrap {
	display: flex;
	justify-content: space-between;
	padding: 24px 30px;
	padding-bottom: 12px;
	@include r($xl) {
		padding: 20px 25px; }
	@include r($lg) {
		padding: 15px 20px; } }


.swiper-nested-btn {
	font-weight: bold;
	font-size: 1rem;
	line-height: normal;
	color: $yellow;
	padding: .5rem 1rem;
	cursor: pointer;
	z-index: 100;
	outline: none;
	@include r($lg) {
		font-size: 0; }
	.icon-arrow {
		width: 27px;
		height: 16px;
		fill: $yellow;
		transition: $transition-base; } }

.swiper-nested-prev {
	left: 14px;
	.icon-arrow {
		transform: rotate(180deg) translateX(16px); } }

.swiper-nested-next {
	right: 14px;
	.icon-arrow {
		transform: translateX(16px); } }

.swiper-nested-btn.swiper-button-disabled {
	pointer-events: none;
	color: $light;
	.icon-arrow {
		fill: $light;
		transform: translateX(8px); } }

.swiper-nested-prev.swiper-button-disabled {
	.icon-arrow {
		transform: rotate(180deg) translateX(8px); } }

.swiper-pagination-nested {
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	z-index: 10;
	@include r($md) {
		display: none; } }

@keyframes shadowpulse {
	0% {
		box-shadow: 0 0 0 0px rgba($yellow, 1); }
	100% {
		box-shadow: 0 0 0 25px rgba($yellow, 0); } }

.ui-element {
	z-index: 50;
	outline: none;
	position: absolute;
	display: inline-block;
	border-radius: 50%;
	width: 30px;
	height: 30px;
	opacity: .3;
	filter: blur(7px);
	background-color: $yellow;
	cursor: pointer;
	right: 0;
	bottom: 0; }

@keyframes bounce {
	0% {
		transform: scale(0); }
	50% {
		transform: scale(1.2); }
	90% {
		transform: scale(.9); }
	100% {
		transform: scale(1); } }



.ui-element:focus {
	outline: none; }

.ui-element.active {
	animation: bounce .4s, shadowpulse 2s infinite;
	opacity: .8; }

@mixin make-el($width, $height, $left, $top) {
	width: (($width/$slide-width) * 100) + %;
	height: (($height/$slide-height) * 100) + %;
	left: (($left/$slide-width) * 100) + %;
	top: (($top/$slide-height) * 100) + %; }


