//sect-title
.sect-title {
	margin: 0;
	padding: 71px 0;
	position: relative;
	font-size: 48px;
	line-height: normal;
	font-weight: bold;
	@include r($sm) {
		font-size: 24px;
		padding: 35px 0; }
	&-text {
		position: relative;
		font-size: 1em;
		font-weight: bold;
		line-height: normal;
		z-index: 20; }
	&-bg {
		color: $light-gray;
		font-size: (170em / 48);
		line-height: normal;
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		z-index: 10;
		@include r($sm) {
			display: none; } }
	&:before {
		position: absolute;
		content: '';
		top: 0;
		bottom: 0;
		right: calc(100% + 32px);
		margin: auto;
		height: 21px;
		width: 100%;
		background-color: $yellow;
		@include r($sm) {
			right: initial;
			left: 0;
			top: initial;
			bottom: 10px;
			width: 20%;
			height: 10px; } } }

.faq {
	margin-bottom: 66px;
	@include r($md) {
		margin-bottom: 33px; }
	&-item {
		margin-bottom: 30px;
		.list-item-link {
			font-weight: normal; } }
	&-item-title {
		margin-bottom: 0;
		line-height: normal; }
	&-item-link {
		font-size: 28px;
		line-height: normal;
		font-weight: bold;
		color: #373a3d;
		position: relative;
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
		white-space: nowrap;
		@include r($xl) {
			font-size: 24px; }
		@include r($md) {
			font-size: 18px; } }
	&-item-link:hover {
		text-decoration: none;
		color: inherit; }
	&-item-link:after {
		display: inline-block;
		vertical-align: middle;
		margin-left: 5px;
		content: '';
		width: 12px;
		height: 12px;
		background-color: transparent;
		border-right: 3px solid $dark;
		border-top: 3px solid $dark;
		transform: rotate(45deg);
		@include r($md) {
			width: 8px;
			height: 8px; } }
	&-item-img {
		display: flex;
		justify-content: center;
		padding: 9px 0; }
	&-item-img img {
		width: 130px;
		height: 130px;
		object-fit: contain; } }

.swiper-pagination-ui {
	@include r($lg) {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	.btn:last-child {
		margin-right: 0 !important; }
	.btn {
		@include r($lg) {
			width: 45%;
			padding-right: 0;
			padding-left: 0; } } }

.list {
	padding: 0;
	margin: 0;
	list-style: none;
	&-item {
		font-size: 1rem;
		line-height: normal;
		margin-bottom: $spacer; }
	&-item.active .list-item-link {
		color: $yellow; }
	&-item-link {
		font-size: 1rem;
		text-decoration: none;
		color: $dark;
		font-weight: bold;
		line-height: normal;
		transition: $transition-base; }
	&-item-link:hover {
		text-decoration: none;
		color: $yellow; } }



//search-page
.search {
	min-height: 300px;
	margin-bottom: 80px; }

.navigation {
	padding: 48px 0;
	@include r($md) {
		padding: 24px 0;
		flex-direction: column-reverse; } }


//breadcrumb
.breadcrumb {
	font-size: 14px;
	line-height: normal;
	color: $dark;
	background-color: $white;
	padding: 0;
	margin: 0;
	&-item + .breadcrumb-item {
		padding-left: .3rem; }
	&-item a {
		color: $dark;
		font-weight: 300; }
	&-item.active {
		font-weight: bold;
		color: $dark; }
	&-item.active:before {
		content: '|';
		color: $dark;
		padding-right: .3rem; } }


.article-content {
	margin-bottom: 96px;
	overflow: hidden;
	@include r($md) {
		margin-bottom: 48px; } }

.custom-tabs {
	position: relative;
	height: 100%;
	z-index: 2;
	background-color: $light;
	@include r($lg) {
		background-color: $white; }
	.list-item-link {
		display: none;
		@include r($lg) {
			display: block;
			padding: 16px 25px;
			font-size: 18px;
			text-align: center;
			border: 2px solid $light;
			font-weight: bold; } } }

.custom-tabs > .tab-pane {
	display: none; }

.custom-tabs > .tab-pane.active {
	display: block; }

.custom-tabs:after {
	position: absolute;
	content: '';
	background-color: $light;
	width: 100%;
	height: 100%;
	right: 5px;
	top: 0;
	z-index: 1;
	transform: translateX(100%);
	@include r($lg) {
		display: none; } }

.custom-tabs-item {
	padding: 60px 100px;
	@include r($lg) {
		background-color: $light;
		padding: 15px 25px; } }

//modal
.modal {
	&-select {
		background-color: $light; }
	&-select:focus {
		background-color: $light; }
	&-select-item:hover {
 }		// background-color: #d3d3d3
	&-dialog {
		@include rmin($sm) {
			margin-top: 83px;
			margin-bottom: 48px; } }
	&-content {
		position: relative;
		padding: 48px 70px;
		border: none;
		box-shadow: 0 2px 4px 0 rgba(155, 155, 155, .5);
		@include r($md) {
			padding: 48px 30px; }
		@include r($sm) {
			padding: 42px 16px;
			padding-top: 60px; } }
	&-content label {
		font-weight: bold;
		line-height: normal;
		width: 100%; }
	&-content .form-group {
		margin-bottom: 1.5rem; }
	&-content .close {
		position: absolute;
		right: -60px;
		top: -60px;
		background-color: transparent;
		border: none;
		cursor: pointer;
		@include r($md) {
			top: 15px;
			right: 15px; }
		@include r($sm) {
			width: 58px;
			height: 58px;
			top: 0;
			right: 0;
			left: 0;
			margin: auto; } }
	&-content .close .icon-close {
		width: 40px;
		height: 40px;
		@include r($md) {
			fill: $dark; }
		@include r($sm) {
			width: 30px;
			height: 30px; } }
	&-content .close:focus {
		outline: none; }
	&-header {
		border-bottom: none;
		flex-wrap: wrap;
		padding: 0; }
	&-title {
		display: block;
		width: 100%;
		font-size: 30px;
		font-weight: bold;
		line-height: normal;
		@include r($sm) {
			font-size: 24px; } }
	&-subtitle {
		width: 100%;
		display: block;
		font-weight: bold;
		@include r($sm) {
			font-weight: normal; } }
	&-body {
		padding: 0; }
	&-footer {
		padding: 0;
		border: none; }
	&-backdrop {
		background-color: rgb(51, 51, 51); }
	&-backdrop.show {
		opacity: .65; } }

.id-label {
	position: relative; }

.id-info {
	border: none;
	position: absolute;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	font-size: 16px;
	line-height: 24px;
	width: 24px;
	height: 24px;
	color: $white;
	background-color: $yellow;
	border-radius: 50%;
	cursor: pointer; }

.id-info:focus {
	outline: none; }

.tooltip {
	font-size: 1rem;
	font-weight: 300;
	line-height: normal;
	&-inner {
		max-width: 360px;
		padding: 20px 25px;
		background-color: $dark;
		color: $white;
		text-align: left;
		@include r($sm) {
			max-width: 220px;
			padding: 10px 15px; } }
	.arrow {
		opacity: 1; } }

.tooltip.show {
	opacity: 1; }












